import Tool from '../DevTools/Tool'
import {
    evalCss
} from '../lib/util'



export default class Navigation extends Tool {
    
    

  constructor() {
    super()
    this.name = 'navigation'
    this._style = evalCss(require('./Navigation.scss'))
    this._tpl = require('./Navigation.hbs')
  }
  
  
  
  init($el) {
    super.init($el)
    
    // CONSTRUCT VIEW
    $el.append( require('./Navigation.hbs')() )
    
    // ADD EVENTS
    this._bindEvent()
    
    // UTILS
    //..
    
  }
  
  
  
  destroy() {
    super.destroy()
    evalCss.remove(this._style)
  }
  
  
  
  clear() {
    this._render()
    return this
  }
  
  
  
  _bindEvent() {
    
    let address = this._$el.find('.eruda-navigation-address').val()
    
    // SEARCH
    this._$el.on('click', '.eruda-navigation-search', function() {
        window.location = address
    })
    
  }
  
  
  
}



